import Vue from 'vue';
import VueRouter from 'vue-router';
import Category from "../views/Category";
import ChatArea from "../views/ChatArea";
import Chart from "../views/Chart";
import DragDrop from "../views/DragDrop";
import Draggable from "../views/Draggable";
import Three from "../views/Three";
import Element from "../views/Element";
import Mobile from "../views/Mobile";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Category',
    component: Category // 新模块都在这里配置
  },
  {
    path: '/chat',
    name: 'Chat',
    component: ChatArea
  },
  {
    path: '/chart',
    name: 'Chart',
    component: Chart
  },
  {
    path: '/drag-drop',
    name: 'DragDrop',
    component: DragDrop
  },
  {
    path: '/nest-drag',
    name: 'NestDrag',
    component: Draggable
  },
  {
    path: '/three',
    name: 'Three',
    component: Three
  },
  {
    path: '/ele',
    name: "Element",
    component: Element
  },
  {
    path: '/mobile',
    name: 'Mobile',
    component: Mobile
  }
]

const router = new VueRouter({routes})

export default router
