<template>
  <div>
    <h3>{{ title }}</h3>
    <pre style="text-align: left">{{ valueString }}</pre>
  </div>
</template>

<script>
export default {
  name: "raw-display",
  props: {
    title: {
      required: true,
      type: String
    },
    value: {
      required: true
    }
  },
  computed: {
    valueString() {
      return JSON.stringify(this.value, null, 2)
    }
  }
}
</script>

