<template>
    <div class="container">
        <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="配置表单" name="first">
                <el-row :gutter="20">
                    <el-col :span="12">
                        <div class="drag_eara">
                            <div class="el-header">可选组件</div>
                            <drag class="drag_item" :transferData="{ type: 'input' }">
                                <span class="handle"></span>
                                <el-input
                                        placeholder="输入框"
                                        disabled
                                        class="form_item_label">
                                </el-input>
                                <el-input
                                        placeholder="请输入内容"
                                        clearable
                                        class="form_component">
                                </el-input>
                            </drag>
                            <drag class="drag_item" :transferData="{ type: 'select' }">
                                <span class="handle"></span>
                                <el-input
                                        placeholder="下拉框"
                                        disabled
                                        class="form_item_label">
                                </el-input>
                                <el-select v-model="value" placeholder="请选择" class="form_component">
                                    <el-option
                                            v-for="item in options"
                                            :key="item.value"
                                            :label="item.label"
                                            :value="item.value">
                                    </el-option>
                                </el-select>
                            </drag>
                            <drag class="drag_item" :transferData="{ type: 'datetime' }">
                                <span class="handle"></span>
                                <el-input
                                        placeholder="输入框"
                                        disabled
                                        class="form_item_label">
                                </el-input>
                                <el-date-picker
                                        type="date"
                                        placeholder="选择日期" class="form_component">
                                </el-date-picker>
                            </drag>
                        </div>
                    </el-col>
                    <el-col :span="12">
                        <div class="el-header">
                            <el-input v-model="form_name" placeholder="为这个表单取个名字吧" class="form_component"></el-input>
                            <el-button type="primary" @click="save">保存</el-button>
                        </div>
                        <drop class="drop_eara" :class="{ over }"
                              @dragover="over = true"
                              @dragleave="over = false"
                              @drop="handleDrop">
                            <SlickList lockAxis="y" v-model="items" tag="ul"
                                       @sort-end="onSortEnd"
                                       @input="onResult"
                            >
                                <SlickItem v-for="(item, index) in items" :index="index" :key="index" tag="li">
                                    <div v-if="item.type === 'input'" class="form_item">
                                        <span class="handle"></span>
                                        <el-input
                                                placeholder="编辑"
                                                clearable
                                                v-model="items[index].label"
                                                class="form_item_label">
                                        </el-input>:
                                        <el-input
                                                placeholder="请输入内容"
                                                clearable
                                                class="form_component">
                                        </el-input>
                                    </div>
                                    <div v-else-if="item.type === 'select'" class="form_item">
                                        <span class="handle"></span>
                                        <el-input
                                                placeholder="编辑"
                                                clearable
                                                v-model="items[index].label"
                                                class="form_item_label">
                                        </el-input>:
                                        <el-select
                                                placeholder="请选择"
                                                v-model="value"
                                                class="form_component">
                                            <el-option
                                                    v-for="item in options"
                                                    :key="item.value"
                                                    :label="item.label"
                                                    :value="item.value">
                                            </el-option>
                                        </el-select>
                                    </div>
                                    <div v-else-if="item.type === 'datetime'" class="form_item">
                                        <span class="handle"></span>
                                        <el-input
                                                placeholder="编辑"
                                                clearable
                                                v-model="items[index].label"
                                                class="form_item_label">
                                        </el-input>:
                                        <el-date-picker
                                                type="datetime"
                                                placeholder="选择日期时间"
                                                class="form_component">
                                        </el-date-picker>
                                    </div>
                                </SlickItem>
                            </SlickList>
                        </drop>
                    </el-col>
                </el-row>
            </el-tab-pane>
            <el-tab-pane label="选择表单" name="second">
                <el-row :gutter="20">
                    <el-col :span="8">
                        <el-radio-group v-model="selected">
                            <el-radio v-for="(item, index) in Object.keys(formStore)"
                                      :key="index" :label="item">{{item}}</el-radio>
                        </el-radio-group>
                    </el-col>
                    <el-col :span="15" class="demo-block">
                        <el-form ref="renderForm" :model="renderForm" label-width="80px" style="width: 44%">
                            <div v-for="(item, index) in formStore[selected]" :key="index">
                                <el-form-item :label="item.label" v-if="item.type === 'input'">
                                    <el-input v-model="renderForm[item.label]" class="form_component"></el-input>
                                </el-form-item>
                                <el-form-item :label="item.label" v-else-if="item.type === 'select'">
                                    <el-select
                                            placeholder="请选择"
                                            v-model="renderForm[item.label]"
                                            class="form_component">
                                        <el-option
                                                v-for="item in options"
                                                :key="item.value"
                                                :label="item.label"
                                                :value="item.value">
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                                <el-form-item :label="item.label" v-else-if="item.type === 'datetime'">
                                    <el-date-picker
                                            type="datetime"
                                            placeholder="选择日期时间"
                                            class="form_component"
                                            v-model="renderForm[item.label]"
                                    >
                                    </el-date-picker>
                                </el-form-item>
                            </div>
                            <el-form-item v-if="formStore[selected]">
                                <el-button type="primary" @click="onSubmit" size="mini">提交</el-button>
                                <el-button size="mini">重置</el-button>
                            </el-form-item>
                        </el-form>
                    </el-col>
                </el-row>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
  import { Drag, Drop } from 'vue-drag-drop';
  import { SlickList, SlickItem } from 'vue-slicksort';

  export default {
    name: "DragDrop",
    components: {
      Drag,
      Drop,
      SlickItem,
      SlickList,
    },
    computed: {
    },
    data: function () {
      return {
        over: false,
        activeName: 'first',
        items: [],
        options: [{
          value: '选项1',
          label: '黄金糕'
        }, {
          value: '选项2',
          label: '双皮奶'
        }, {
          value: '选项3',
          label: '蚵仔煎'
        }],
        value:'',
        form_name: '',
        formStore: {},
        selected: '',
        renderForm: {}
      };
    },
    methods: {
      handleDrop(data) {
        this.over = false;
        this.items.push({type: data.type, label: ''})
      },
      // onSortEnd({ event, newIndex, oldIndex }) {
      //   console.log(event, newIndex, oldIndex)
      // },
      onSortEnd() {},
      onResult(arr) {
        this.items = arr
      },
      save() {
        if (this.form_name === '') {
          this.$message.error('自定义表单名称不能为空')
          return
        }
        this.formStore[this.form_name] = this.items.map(item => item)
        this.$message.success('保存成功')
      },
      handleClick() {},
      onSubmit() {
        console.log(this.renderForm)
      }
    },
  }
</script>

