<template>
    <el-tabs v-model="activeName" class="tab_pan">
        <el-tab-pane label="考生答题" name="first" :style="panHeight">
            <el-row>
                <el-col :span="8">
                    <MarkdownPreview :initialValue="test"/>
                </el-col>
                <el-col :span="16">
                    <div class="tool_bar">
                        <el-select v-model="cmOptions.mode" placeholder="请选择">
                            <el-option
                                    v-for="item in modes"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                        <i class="el-icon-refresh"></i>
                        <i class="el-icon-full-screen"></i>
                    </div>
                    <!-- bidirectional data binding（双向数据绑定） -->
                    <codemirror v-model="code" :options="cmOptions" @changes="onCodeChange" :style="codemirrorStyle" />
                    <div class="footer">
                        <el-button type="primary">提交运行</el-button>
                    </div>
                </el-col>
            </el-row>
        </el-tab-pane>
        <el-tab-pane label="考官出题" name="second">
            <markdown :styleObject="markDownHeight" @sync-test="handleSync"/>
        </el-tab-pane>
    </el-tabs>
</template>

<script>
  import { MarkdownPreview } from 'vue-meditor'
  import markdown from "./markdown/Markdown";
  import { codemirror } from 'vue-codemirror'
  import 'codemirror/lib/codemirror.css'
  // 引入主题后还需要在 options 中指定主题才会生效
  import 'codemirror/theme/duotone-light.css'

  // 需要引入具体的语法高亮库才会有对应的语法高亮效果
  // codemirror 官方其实支持通过 /addon/mode/loadmode.js 和 /mode/meta.js 来实现动态加载对应语法高亮库
  // 但 vue 貌似没有无法在实例初始化后再动态加载对应 JS ，所以此处才把对应的 JS 提前引入
  import 'codemirror/mode/javascript/javascript.js'
  import 'codemirror/mode/go/go.js'
  import 'codemirror/mode/shell/shell.js'
  import 'codemirror/mode/sql/sql.js'

  export default {
    name: "TestArea",
    components: {
      markdown,
      MarkdownPreview,
      codemirror
    },
    props: {
      test: {
        type: String
      }
    },
    data() {
      return {
        activeName: 'first',
        markDownHeight: 100,
        panHeight: { height: '100%'},
        codemirrorStyle: { height: '500px' },
        code: 'const a = 10',
        cmOptions: {
          // codemirror options
          mode: 'javascript',
          tabSize: 4,
          theme: 'duotone-light',
          lineNumbers: true,
          // more codemirror options, 更多 codemirror 的高级配置...
        },
        modeValue: 'javascript',
        modes: [
          {
              value: 'javascript',
              label: 'Javascript'
          },
          {
              value: 'java',
              label: 'Java'
          },
          {
              value: 'go',
              label: 'Go'
          },
          {
              value: 'x-sh',
              label: 'Shell'
          },
          {
              value: 'x-sql',
              label: 'SQL'
          }
        ],
      }
    },
    mounted() {
      // 获取浏览器屏幕高度自动调整聊天室高度
      this.markDownHeight = document.documentElement.clientHeight-75
      this.panHeight.height = document.documentElement.clientHeight-75 +'px'
      this.codemirrorStyle.height = document.documentElement.clientHeight-170 + 'px'
      window.onresize = () => {
        this.markDownHeight.height = document.documentElement.clientHeight-75
        this.panHeight.height = document.documentElement.clientHeight-75 +'px'
        this.codemirrorStyle.height = document.documentElement.clientHeight-170 + 'px'
      };
    },
    methods: {
      onCodeChange(instance, changeObj) {
        console.log(instance)
        console.log(changeObj)
      },
      handleSync(data) {
        console.log('test-data', data)
        this.$emit('sync-to-all', data)
      }
    }
  }
</script>

<style scoped>
    .tool_bar {
        position: relative;
        background-color: #a3bad2;
    }
    .tool_bar .el-icon-refresh {
        margin-left: 20px;
    }
    .tool_bar .el-icon-full-screen {
        position: absolute;
        right: 10px;
        top: 10px;
    }
</style>
